import {  h } from 'hyperapp';
import { url } from './effects';

export default ({loadingUsers, allPhotos}) => 
	<div>
		{loadingUsers ? 
			<p>loading photos...</p> :
			<PhotosList photos={allPhotos} />
		}
	</div>;

const PhotosList = ({ photos }) => 
	photos.map(photo =>
		<img
			key={photo.id}
			src={url+photo.url}
			alt={photo.name}
			width={350} 
		/>
	);